* {
  margin:0;
  padding:0;
}


body {
  background: #000;
}

a {
  text-decoration: none;
  color: #010101;
}


.app-title {
  font-family: 'Luckiest Guy', cursive;
  color: #fefefe;
  padding:20px;
  text-shadow: 0px 3px 2px #994BB6;
}

.app-title:before {
  content: "";
  width:100px;
  height:100px;
  background: url("https://img.icons8.com/metro/52/000000/llama.png");
  background-size:10px;
}

.menu-container {
  width:100%;
  display:flex;
  flex-wrap:wrap;
  text-align:center;
  height:0px;
  opacity: 0;
  transition: height .6s, opacity .3s;
  /* border-bottom:3px solid #994BB6; */
  /* border-top:3px solid #994BB6; */
}

.menu-item {
  color:#fff;
  width:100%;
  font-size:150%;
  margin: 10px auto;
  padding: 10px;
  text-transform: uppercase;
  text-shadow: 0px 3px 2px #994BB6;
  font-weight:900;
  outline: none;
}

.menu-button {
  position:absolute;
  top:2.5px;
  right:0px;
  background:none;
  border:none;
  color:#fff;
  font-size:200%;
  padding:10px 20px;
}

.view-container {
  width:100%;
  margin:0 auto;
  display: flex;
  flex-wrap:wrap;
  background:#000;
}

.item-card {
  width: 70%;
  margin: 30px auto;
  color:#efefef;
  opacity:0;
  animation: enter 1s linear forwards;
  text-align:center;
}

.trans-img {
  width: 70%;
  border-bottom-right-radius:30%;
  border-bottom-left-radius:30%;
}

.cost-container {
  display:flex;
  width:100%;
  justify-content:center;
}
.cost {
  font-size: 150%;
  margin-left:-10px;
}
.currency {
  height:20px;
  width:20px;
  position: relative;
  top:7px;
  left:-10px;
}

.rare {
  background: radial-gradient(#78AECE, #000 45%);
  color: #78AECE;
}
.uncommon {
  background: radial-gradient(#93CF86, #000 45%);
  color: #93CF86;
}
.epic {
  background: radial-gradient(#C680EC, #000 40%);
  color: #C680EC;
}
.legendary {
  background: radial-gradient(#F56623, #000 50%);
  color: #F56623;
}






@media screen and (min-width: 600px){
  .item-card {
    width: 50%;
  }
}
@media screen and (min-width: 1200px){
  .item-card {
    width: 30%;
  }
}


@keyframes enter {
  100% {
    /* transform: scale(1); */
    opacity:1;
  }
}